import VuePerfectScrollbar from './scroll'

// vue,
if (typeof window !== 'undefined' && window.Vue) {
  // eslint-disable-next-line
  Vue.component('vue-perfect-scrollbar', VuePerfectScrollbar)
}

export default {
    install (Vue) {
        Vue.component('vue-perfect-scrollbar', VuePerfectScrollbar)
    }
}

export {
    VuePerfectScrollbar
}
