
import { Message } from 'element-ui'
const MyPlugin = {};

MyPlugin.install = function (Vue) {
    // --
    Vue.prototype.changeDate = function (timeStamp) {
        if(timeStamp){
            let date = new Date();
            date.setTime(timeStamp);
            let y = date.getFullYear();
            let m = date.getMonth() + 1;
            m = m < 10 ? ('0' + m) : m;
            let d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            return y + '-' + m + '-' + d;
        }
        else{
            return '-'
        }
    };
    // -- 
    Vue.prototype.changeTime = function (timeStamp){
        if(timeStamp){
            let date = new Date();
            date.setTime(timeStamp);
            let y = date.getFullYear();
            let m = date.getMonth() + 1;
            m = m < 10 ? ('0' + m) : m;
            let d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            let h = date.getHours();
            h = h < 10 ? ('0' + h) : h;
            let minute = date.getMinutes();
            let second = date.getSeconds();
            minute = minute < 10 ? ('0' + minute) : minute;
            second = second < 10 ? ('0' + second) : second;
            return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
        }
        else{
            return '-'
        }
    };
    //
    Vue.prototype.floatful = function (num,name){
        if(parseFloat(num)>=0){
            this[name] = parseFloat(num);
        }
        else if(num == ''){

        }
        else{
            this[name] = '';
            //element-UI
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });
        }
    };
    //
    Vue.prototype.intful = function (num,name){
        if(parseInt(num)>=0){
            this[name] = parseInt(num);
        }
        else if(num == ''){

        }
        else{
            this[name] = '';
            //element-UI
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });
        }
    };
    //
    /**
     * @return {number}
     */
    Vue.prototype.StrchangeTime = function (str) {
        if(str.length>0){
            let date = str.substring(0,19);
            date = date.replace(/-/g,'/');
            return new Date(date).getTime();
        }
    };

    //
    Vue.directive('clickoutside', {
        bind:function(el,binding,vnode){
            function documentHandler(e){
                if(el.contains(e.target)){
                    return false;
                }
                if(binding.expression){
                    binding.value(e)
                }
            }
            el._vueClickOutside_ = documentHandler;
            document.addEventListener('click',documentHandler);
        },
        unbind:function(el,binding){
            document.removeEventListener('click',el._vueClickOutside_);
            delete el._vueClickOutside_;
        }
    });

};

export default MyPlugin



